

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary-variant), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    width: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition:var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: var(--color-primary-variant);
    cursor: defautlt;
}

.about__icon {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}


.about__card h5 {
    font-size: 0.95rem;
    color: var(--color-white);
}
.about__card small {
    font-size: 0.9rem;
    color: var(--color-light);
}

.about__content {
    margin: auto;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-white);
    text-align: justify;
}

/* Media Queies (Medium Devices) */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content {
        margin: 1rem 0 1.5rem;
    }
}

/* Media Queies (Small Devices) */
@media screen and (max-width: 600px) {
    .about__me {
        width: 50%;
        margin: 0 auto 0.5rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        margin: 1rem;
    }

    .about__content {
        text-align: left;
    }

    .about__content p {
        margin: 1.5rem;
        font-size: 0.9rem;
    }
}